import helper from '../../common/helper.js';
export default {
	data() {
		return {
			baseinfoArray: {},
			renwuArray: {},
		};
	},
	onLoad() {
		this.getdata();
	},
	onPullDownRefresh: function() { //下拉刷新
		this.getdata(true);
	},
	onShow() {
		this.getdata()
	},
	methods: {
		getdata(isdown) {
			helper.islogin(true);
			uni.showLoading({
				mask: true
			})
			var userId = helper.getstate().userid;
			uni.request({
				url: helper.websiteUrl + '/index/getindex',
				method: 'GET',
				data: helper.postdata({
					userid: userId,
					page: 1,
					num: 10,
				}),
				success: (res) => {
					if (res.data.code == 200) {
						this.baseinfoArray = res.data.data.baseinfo;
						this.renwuArray = res.data.data.tasklist;
					} else {
						helper.goout(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					if (res.data.data.tasklist.IsGet > 0) { //该任务用户是否已经领取过
						this.IsGet = '已领取'
					} else {
						this.IsGet = '领取'
					}
					uni.hideLoading();
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请下拉刷新重试！'
					});
				},
				complete: () => {
					if (isdown) {
						uni.stopPullDownRefresh(); //刷新停止
					}
				}
			});
		},
		openTixian() { //打开提现页面
			uni.showToast({
				icon: 'none',
				title: '该功能暂未开通！'
			});
			// 				uni.navigateTo({
			// 					url: '../Tixian/tixianindex',
			// 					success: res => {},
			// 					fail: () => {},
			// 					complete: () => {}
			// 				});
		},
		openJianli() { //打开领取奖励页面
			uni.navigateTo({
				url: 'lingqujiangli',
			});
		},
		openbawangcan() { //打开霸王餐页面
			uni.navigateTo({
				url: 'bawangcan',
			});
		},
		openmytask() { //打开我的任务页面
			uni.navigateTo({
				url: '../task/mytask',
			});
		},
		openinfo(e) {
			uni.navigateTo({
				url: '../task/Taskdetails?rwid=' + e.currentTarget.dataset.rwid
			});
		}
	}
}
